@use "../../../styles/mixins.scss" as m;
@use "../../../styles/variables.scss" as v;

.buttons_item__transparent {
  background: transparent;
  color: v.$white;
  // @include m.FadeInLeft;
  @include m.trans;
  &:hover {
    transform: scale(1.1);
  }
  .buttons_item__icon {
    width: 35px;
    margin-right: 5px;
    height: 100%;
    @include m.pcS {
      width: m.rem(35);
      margin-right: m.rem(5);
    }
  }
  &.buttons_item {
    display: flex;
    align-items: center;
    font-size: 18px;
  }
}
